
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#addgif {
  margin-top: 20px;
}
#addgif > div {
  overflow: hidden;
  padding: 10px 20px;
  background: #fff;
  border-bottom: 1px solid #ddd;
}
#addgif > div .fr {
  border: none;
  text-align: right;
  height: 25px;
}
#addgif > div .fr:focus {
  outline: none;
}
#addgif .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 10px 0;
}



